import { connect } from 'react-redux';
import Time from '../../../../Components/UIElement/Forms/Field/Time';
import { inputOnchangeValue } from '../../../../Redux/actions/form.actions';

const mapState = (state) => ({
  formValues: state.formReducer,
});

const mapDispatch = (dispatch) => ({
  inputOnchangeValue: (payload) => {
    dispatch(inputOnchangeValue(payload));
  },
});

export default connect(mapState, mapDispatch)(Time);
