import React, { useState, useEffect } from 'react';

import './stylesAdminProducts.scss';
import Modal from '../../UIElement/Modal/Modal';
import Pagination from '../../UIElement/Pagination/PaginationContainer';
import AdminProductList from '../../UIElement/AdminList/AdminProductList';
import FormProduct from '../../UIElement/Forms/FormProduct';

const AdminProducts = ({
  fetchAllAdminProducts,
  products,
  collections,
  users,
  types,
  createOrModifyProduct,
  getIdFromObject,
  eraseFormState,
  deleteProduct,
}) => {
  const [modal, setModal] = useState(false);
  const [modalContent, setModalContent] = useState(null);

  useEffect(() => {
    fetchAllAdminProducts();
  }, [fetchAllAdminProducts]);
  const openModal = () => setModal(true);
  const closeModal = () => setModal(false);

  const usersIllustrator = users.filter(
    (user) => user.role === 'Editeur' || user.role === 'Illustrateur'
  );

  return (
    <>
      <Modal
        show={modal}
        onCancel={closeModal}
        style={{ textAlign: 'center' }}
        button={
          <button onClick={closeModal} style={{ backgroundColor: 'red' }}>
            fermer
          </button>
        }
        content={modalContent}
      />
      <div className="product-list-box">
        <div className="admin-title">
          <h2>Produits</h2>
          <button
            onClick={() => {
              openModal();
              setModalContent(
                <FormProduct
                  collections={collections}
                  usersIllustrator={usersIllustrator}
                  types={types}
                  createOrModifyProduct={createOrModifyProduct}
                />
              );
            }}
          >
            Ajouter un produit
          </button>
        </div>
        {products.length === 0 ? <p>Rien à afficher pour l'instant.</p> : null}
        <div className="product-list-container">
          <Pagination
            data={products}
            Component={AdminProductList}
            eraseFormState={eraseFormState}
            getIdFromObject={getIdFromObject}
            deleteProduct={deleteProduct}
            openModal={openModal}
            setModalContent={setModalContent}
            collections={collections}
            usersIllustrator={usersIllustrator}
            types={types}
            createOrModifyProduct={createOrModifyProduct}
          />
        </div>
      </div>
    </>
  );
};

export default AdminProducts;
