import React from 'react';
import { v4 as uuidv4 } from 'uuid';

import './stylesField.scss';

const SelectMultiple = ({
  label,
  type,
  options,
  formValues,
  existingvalue = [],
  inputOnchangeValue,
  ...props
}) => {
  const inputValue =
    formValues[props.name] !== undefined
      ? formValues[props.name]
      : existingvalue.map((value) => value[0]);

  return (
    <div className="input-box" style={{ height: '230px' }}>
      <label className="label">{label}</label>
      <select
        type={type}
        className="input"
        value={inputValue || []}
        style={{ minHeight: '200px' }}
        onChange={(event) => {
          let value = Array.from(
            event.target.selectedOptions,
            (option) => +option.value
          );
          inputOnchangeValue({ [props.name]: value });
        }}
        {...props}
      >
        {options.map((option) => (
          <option value={option.id} key={uuidv4()}>
            {option.label ||
              option.name ||
              `${option.firstname} ${option.lastname}`}
          </option>
        ))}
      </select>
    </div>
  );
};

export default SelectMultiple;
