import { connect } from 'react-redux';
import AdminProducts from '../../Components/Admin/AdminProducts/AdminProducts';
import {
  fetchAllAdminProducts,
  createOrModifyProduct,
  deleteProduct,
} from '../../Redux/actions/listProducts.actions';
import {
  getIdFromObject,
  eraseFormState,
} from '../../Redux/actions/form.actions';

const mapState = (state) => {
  return {
    products: state.listProductsReducer.products,
    editions: state.listProductsReducer.editions,
    tags: state.listProductsReducer.tags,
    collections: state.listProductsReducer.collections,
    users: state.listProductsReducer.users,
    types: state.listProductsReducer.types,
  };
};

const mapDispatch = (dispatch) => ({
  fetchAllAdminProducts: () => {
    dispatch(fetchAllAdminProducts());
  },
  createOrModifyProduct: () => {
    dispatch(createOrModifyProduct());
  },
  getIdFromObject: (payload) => {
    dispatch(getIdFromObject(payload));
  },
  eraseFormState: () => {
    dispatch(eraseFormState());
  },
  deleteProduct: () => {
    dispatch(deleteProduct());
  },
});

export default connect(mapState, mapDispatch)(AdminProducts);
