import React, { useState } from 'react';

import './stylesField.scss';

const regex = /^([0-2][0-9]|(3)[0-1])(\/)(((0)[0-9])|((1)[0-2]))(\/)\d{4}$/;

const Time = ({
  inputOnchangeValue,
  formValues,
  existingvalue,
  label,
  ...props
}) => {
  const [onChangeValue, setOnChangeValue] = useState(null);

  const convertExistingValueToDate = new Date(existingvalue);

  const date = isNaN(convertExistingValueToDate.getDate())
    ? `01`
    : convertExistingValueToDate.getDate() < 10
    ? `0${convertExistingValueToDate.getDate()}`
    : `${convertExistingValueToDate.getDate()}`;

  const month = isNaN(convertExistingValueToDate.getMonth())
    ? `01`
    : convertExistingValueToDate.getMonth() + 1 < 10
    ? `0${convertExistingValueToDate.getMonth() + 1}`
    : `${convertExistingValueToDate.getMonth()}`;

  const year = isNaN(convertExistingValueToDate.getFullYear())
    ? `2020`
    : convertExistingValueToDate.getFullYear();

  const convertedToTime = `${date}/${month}/${year}`;

  const valueInput =
    onChangeValue !== null
      ? onChangeValue
      : isNaN(convertExistingValueToDate.getDate())
      ? `Veuillez saisir une date`
      : convertedToTime;
  return (
    <div className="input-box">
      <label className="label">{label}</label>
      <input
        {...props}
        className="input"
        value={valueInput}
        onChange={(event) => {
          setOnChangeValue(event.target.value);
          let valueToRedux = '';
          if (regex.test(event.target.value)) {
            const initial = event.target.value.split('/');
            const convertToUkDate = [initial[1], initial[0], initial[2]].join(
              '/'
            );
            valueToRedux = new Date(convertToUkDate).toISOString();
          }
          inputOnchangeValue({ [props.name]: valueToRedux });
        }}
      />
    </div>
  );
};

export default Time;
