import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import FormProduct from '../../UIElement/Forms/FormProduct';
import MultipleFiles from '../../UIElement/Forms/Field/MultipleFiles';

const AdminProductList = ({
  item,
  eraseFormState,
  getIdFromObject,
  deleteProduct,
  openModal,
  setModalContent,
  collections,
  usersIllustrator,
  types,
  createOrModifyProduct,
}) => {
  const convertToDate = (date) => {
    const convertExistingValueToDate = new Date(date);

    return `${convertExistingValueToDate.getDate()}/${
      convertExistingValueToDate.getMonth() + 1
    }/${convertExistingValueToDate.getFullYear()}`;
  };
  return (
    <div className="product-list-item" style={{ height: '350px' }}>
      <div
        className="product-list-item_img"
        style={{
          background: `url(https://loupargent-oclock.fr/img/${item.picture_file[0]})`,
          backgroundSize: 'cover',
        }}
      ></div>
      <h1 className="product-list-item_title">{item.name}</h1>
      <span
        className="product-list-item_delete"
        onClick={() => {
          eraseFormState();
          getIdFromObject({ id: item.id });
          window.confirm('Etes vous sûr de vouloir supprimer ce produit ?') &&
            deleteProduct();
        }}
      >
        <FontAwesomeIcon icon="trash-alt" />
      </span>
      <div
        className="product-list-item_description"
        style={{ minHeight: '10px' }}
      >
        Description : {item.synopsys.slice(0, 50)}
      </div>
      <div className="product-list-item_infos">
        <span>N°{item.id}</span>
        <span>{item.price}€</span>
        <span>stock : {item.stock_quantity}</span>
      </div>
      <div className="product-list-item_infos">
        <span>{item.type_name}</span>
      </div>
      <div className="product-list-item_diverse">
        <span>Collection : {item.collection_name}</span>
        <span>Taille : {item.size}</span>
        <span>Date de parution : {convertToDate(item.release_date)}</span>
      </div>
      <div className="product-list-item_modifications">
        <button
          onClick={() => {
            openModal();
            setModalContent(<MultipleFiles id={item.id} />);
          }}
        >
          <FontAwesomeIcon style={{ margin: '0 0.5rem' }} icon="file-image" />
          Photo
        </button>
        <button
          onClick={() => {
            openModal();
            setModalContent(
              <FormProduct
                data={item}
                collections={collections}
                usersIllustrator={usersIllustrator}
                types={types}
                createOrModifyProduct={createOrModifyProduct}
                getIdFromObject={getIdFromObject}
              />
            );
          }}
        >
          <FontAwesomeIcon icon="pen" />
          Modifier
        </button>
      </div>
    </div>
  );
};

export default AdminProductList;
